.sidebar_open {
    -webkit-transform: translateX(0) !important;
    -moz-transform: translateX(0) !important;
    transform: translateX(0) !important;
    opacity: 1 !important;
}
.sidebar_container {
  
    position: fixed;
    left: 0;
    top: 0;
    z-index: 20;
    max-width: 100%;
    background-color: #000;
    overflow: scroll;
    box-shadow: 0 0 12px rgba(0,0,0,.1);
    -webkit-box-shadow: 0 0 12px rgba(0,0,0,.1);
    -webkit-animation: styles_fadein__2pE0b .3s;
    -moz-animation: styles_fadein__2pE0b .3s;
    animation: styles_fadein__2pE0b .3s;
    -moz-transition: opacity .25s,translateX 2s;
    -moz-transition: .3s;
    transition: .3s;
    -webkit-transform: translateX(-100%);
    -moz-transform: translateX(-100%);
    transform: translateX(-100%);
    opacity: 0;
    div.row{
        max-width: 100%;
        width: 100%;
        margin: 0;
        height: 100vh;
        max-height: 100vh;
    }
}

.socialBar {
    background-color: #000;
    min-width: 60px;
    max-width: 60px;
    height: 100%;
    z-index: 40;
    div.socialWrap{
        bottom: 10px;
        left: 10px;
        position: fixed;
    }
}

@media screen and (min-width:993px) {
    .sidebar_container {
     top:50px;
     max-width:410px;
        div.row{
            max-width: 410px;
            height: -moz-calc(100vh - 50px);
            height: calc(100vh - 50px);
            max-height: -moz-calc(100vh - 50px);
            max-height: calc(100vh - 50px);
        }
    }
}

.sidebar_row {
    max-width: 410px;
    height: -moz-calc(100vh - 50px);
    height: calc(100vh - 50px);
    max-height: -moz-calc(100vh - 50px);
    max-height: calc(100vh - 50px);
}
button.closeBtn{
    font-size: 30px;
    line-height:24px;
    :focus{
        outline: none;
    }
}

.sidebar_row {
    max-width: 100%;
    width: 100%;
    margin: 0;
    height: 100vh;
    max-height: 100vh;
}

.iconLink {
    font-size: 1rem;
    width: 40px;
    text-align: center;
    -moz-transition: border-left-color .3s;
    transition: border-left-color .3s;
    a{
        display: inline-block;
        width: 40px;
        height: 40px;
        color: #fff!important;
    }
    a:hover{
        background-color: #fff;
        color:#000 !important;
    }
}
.iconLink_hz{
    background-color: hsla(0,0%,100%,.15);
    margin: 10px 7px;
}

.navbar-dark .socialBar .nav-link{
    color: #fff;
}
.navMenu{
    padding-top: 1.5rem;
    overflow: scroll;
    background-color: #fff;
    font-family: Avenir Next Cyr,sans-serif;
}
.header{
    display: -webkit-flex;
    display: -moz-box;
    display: flex;
    -webkit-justify-content: space-between;
    -moz-box-pack: justify;
    justify-content: space-between;
    -webkit-align-items: center;
    -moz-box-align: center;
    align-items: center;
    padding: 0;
}

.root1{
    width: 100%;
    background-color: transparent;
    border-bottom: 1px dashed rgba(0,0,0,.1);
}
.header, .root{
    border: none;
    background-color: transparent;
}
.menuLink {
    cursor: pointer;
    text-transform: uppercase;
    color: #2621a3;
    font-size: 16px;
    font-family: Poppins,sans-serif;
    font-weight: 500;
    padding: .8rem 1rem .7rem 0;
    -moz-transition: color .3s,background-color .3s,border-color .3s;
    transition: color .3s,background-color .3s,border-color .3s;
    -webkit-transition: color .3s,background-color .3s,border-color .3s;
}
.iconWrapper {
    display: -webkit-flex;
    display: -moz-box;
    display: flex;
    -webkit-align-items: center;
    -moz-box-align: center;
    align-items: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border: 1px solid rgba(0,0,0,.1);
    cursor: pointer;
}
.submenuWrapper{
    background-color: rgba(0,0,0,.038);
    border-radius: 8px;
    padding: 13px 25px;
    margin: 23px 0 28px;
    a {
        color: rgb(33, 37, 41);
    }
}
.menu_btm{
    border-top: 2px dashed hsla(0,0%,100%,.15);
    margin-top: 32px;
    padding-top: 28px;
}


@media screen and (max-width:992px) {
    .menuLink{
        color: #ffffff;
    }
    .iconWrapper{
        color:#fff;
    }
    .submenuWrapper{
        margin: 0 auto;
        padding: 0 25px;
        a{
            color: #ffffff;
            font-size: 14px;
            position: relative;
            padding: 8px 16px 8px 10px;
        }
        a:before{
            content: "";
            display: block;
            position: absolute;
            left: 0;
            width: 3px;
            height: 3px;
            background-color: hsla(0,0%,100%,.5);
            top: 17px;
            border-radius: 50%;
            -webkit-border-radius: 50%;
        }
    }
}

.mainText{

}
.links{}
.profileIcon{}
.userInfo{
    position: absolute;
    left: 100%;
    bottom: 0;
    width: 360px;
    background-color: #000;
    position: absolute;
    z-index: 999;
    color: #fff;

    .mainText{
        word-break: break-all;
    }
    .links{
        font-size: 1rem;
        text-transform: none;
        a{display: inline}
    }
    .profileIcon{
        font-size: 3.5rem;
        padding-bottom: 1rem;
    }
}
@media screen and (max-width:992px) {
    .userInfo {
        left: 0;
        width: 100%;
        top:100%;
        height: 200px;
    }
}
.vHidden{
    visibility: hidden;
    opacity: 0;
}

