.menuBtn{
  border: 1px solid #fff;
  width: 30px;
  height: 30px;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-justify-content: center;
  -moz-box-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
}
.menuBtn:hover .rootClose {
    -webkit-transform: rotate(225deg) translateY(1px);
    -moz-transform: rotate(225deg) translateY(1px);
    transform: rotate(225deg) translateY(1px);
}
.rootClose{
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transform-origin: center;
  -moz-transform-origin: center;
  transform-origin: center;
  -moz-transition: .4s;
  transition: .4s;
  font-size: 15px;
}

.rootOpen {
  display:block;
  float:none;
  width:29px;
  height:29px;
  overflow:hidden;
  position:relative;
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
  transition:left .3s ease-in-out,right .3s ease-in-out,opacity .3s ease-in-out,-webkit-transform .3s ease-in-out;
  -moz-transition:transform .3s ease-in-out,left .3s ease-in-out,right .3s ease-in-out,opacity .3s ease-in-out,-moz-transform .3s ease-in-out;
  transition:transform .3s ease-in-out,left .3s ease-in-out,right .3s ease-in-out,opacity .3s ease-in-out;
  transition:transform .3s ease-in-out,left .3s ease-in-out,right .3s ease-in-out,opacity .3s ease-in-out,-webkit-transform .3s ease-in-out,-moz-transform .3s ease-in-out
}
.rootOpen>* {
    display:block;
    position:absolute;
    width:16px;
    height:1px;
    border:none;
    background:none;
    left:6px;
    top:50%;
    transition:left .3s ease-in-out,opacity .3s ease-in-out,-webkit-transform .3s ease-in-out;
    -moz-transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,-moz-transform .3s ease-in-out;
    transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out;
    transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,-webkit-transform .3s ease-in-out,-moz-transform .3s ease-in-out
}
.rootOpen>:first-child {
    margin-top:-5px
}
.rootOpen>:nth-child(3) {
    margin-top:5px
}
.rootOpen>:before {
    content:"";
    display:block;
    width:100%;
    height:100%;
    position:absolute;
    top:0;
    left:0;
    background-color:#fff;
    transition:left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease,-webkit-transform .3s ease-in-out;
    -moz-transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease,-moz-transform .3s ease-in-out;
    transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease;
    transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease,-webkit-transform .3s ease-in-out,-moz-transform .3s ease-in-out
}
.rootOpen>:nth-child(4),
.rootOpen>:nth-child(5) {
    opacity:0;
    -moz-transition:none;
    transition:none;
    -webkit-transition:none
}
.rootOpen>:first-child:after,
.rootOpen>:nth-child(2):after,
.rootOpen>:nth-child(3):after {
    content:"";
    display:block;
    width:100%;
    height:100%;
    position:absolute;
    top:0;
    left:-200px;
    background-color:#fff;
    transition:left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease,-webkit-transform .3s ease-in-out;
    -moz-transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease,-moz-transform .3s ease-in-out;
    transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease;
    transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease,-webkit-transform .3s ease-in-out,-moz-transform .3s ease-in-out;
    -webkit-transition:transform .3s ease-in-out,left .3s ease-in-out,opacity .3s ease-in-out,background-color .3s ease
}
.rootOpen>:nth-child(2):after,
.rootOpen>:nth-child(2):before {
    -moz-transition-delay:.2s;
    transition-delay:.2s;
    -webkit-transition-delay:.2s
}
.rootOpen>:nth-child(3):after,
.rootOpen>:nth-child(3):before {
    -moz-transition-delay:.4s;
    transition-delay:.4s;
    -webkit-transition-delay:.4s
   }
.rootOpen:hover>:first-child:before,
.rootOpen:hover>:nth-child(2):before,
.rootOpen:hover>:nth-child(3):before {
    left:200%
}
.rootOpen:hover>:first-child:after,
.rootOpen:hover>:nth-child(2):after,
.rootOpen:hover>:nth-child(3):after {
    left:0
}


